.search {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 66.6%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */
  border-radius: 0.5rem;
}
 
.frame {
position: absolute;
top: 0;
left: 0;
bottom: 0;
right: 0;
width: 100%;
height: 100%;
}

  @media only screen and (max-width: 600px) {
    .search {
        margin-left: -50%;
        width: 340px;
        height: 500px;
    }
  }
