.footer-container {
  background-color: rgba(28, 27, 27, 0.466);
  padding: 0rem 0 0rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: static;
  left: 0;
  bottom: 0;
  width: 100%;
  }

  .footer-links {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
  }
  
  .footer-link-wrapper {
    display: flex;
  }
        
  .footer {
    max-width: 1000px;
    width: 100%;
  }
  
  .footer-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: 16px auto 0 auto;
  }
    
  .website-rights {
    color: #fff;
    margin-bottom: 16px;
  }
  .contacts {
    color: #fff;
    margin-bottom: 16px;
  }

  .contacts > h1 {
    font-size: medium;
    display: inline;
    margin: 1rem;
  }
  .email {
    color: #fff;
  }

  .email:hover {
    color: #fff;
    background-color: gray;
  }


  
  